    position: relative;
    height: 100%;
    overflow: hidden;
    .tool-wrapper {
        position: absolute;
        bottom: 0;
        left: 10px;
        width: 40px;
        color: $black;
        text-align: center;
        z-index: 4;
        .auto-position, .reset-position {
            margin: 5px auto;
            width: 34px;
