            }
        }
        .zoom-area {
            margin: 5px auto;
            width: 34px;
            background: $whiteDefault;
            border: 1px solid #d6d6d6;
            border-radius: 2px;
            box-shadow: 0 0 2px rgba(0, 0, 0, 0.15);
            .zoom-in, .zoom-out {
                height: 30px;
