                font-size: 20px;
                cursor: pointer;
                &:hover {
                    color: $blueDefault;
                }
            }
            .zoom-out {
                padding-top: 0;
                &:hover {
                    .minus {
                        border-color: $blueDefault;
                    }
                }
            }
            .minus {
                display: inline-block;
