<template>
    <div class="tag-input">
        <div v-if="formMode">
            <el-input
                type="text"
                v-model="inputValue"
                :disabled="!editable"
                :placeholder="placeholder">
            </el-input>
            <span v-show="!validateInfo.valid" class="common-error-tip error-info">{{validateInfo.message}}</span>
        </div>
        <span v-else class="rf-view-value">{{(value === 'undefined' || value === '') ? '--' : value}}</span>
    </div>
</template>
<script>
import '@/utils/i18n.js'
import { getFormMixins } from '../formMixins.js'

const inputAttrs = {
    placeholder: {
        type: String,
        required: false,
        default: '',
        desc: 'placeholder'
    },
    value: {
        type: String,
        required: false,
        default: ''
    }
}
export default {
    name: 'TagInput',
    mixins: [getFormMixins(inputAttrs)],
    computed: {
        inputValue: {
            get () {
                return this.value
            },
            set (val) {
                this.updateForm(val)
            }
        }
    }
}
</script>
<style lang="scss" scoped>
.tag-input {
    /deep/ .el-input__inner {
        height: 36px;
        line-height: 36px;
        padding: 0 10px;
    }
}
</style>

