<template>
    <div class="tag-int">
        <div v-if="formMode">
            <el-input
                type="number"
                :disabled="!editable"
                :placeholder="placeholder"
                v-model="intValue">
            </el-input>
            <span v-show="!validateInfo.valid" class="common-error-tip error-info">{{validateInfo.message}}</span>
        </div>
        <span v-else class="rf-view-value">{{(value === 'undefined' || value === '') ? '--' : value}}</span>
    </div>
</template>
<script>
import '@/utils/i18n.js'
import { getFormMixins } from '../formMixins.js'

const intAttrs = {
    placeholder: {
        type: String,
        required: false,
        default: '',
        desc: 'placeholder'
    },
    value: {
        type: [Number, String],
        required: false,
        default: 0
    }
}
export default {
    name: 'TagInt',
    mixins: [getFormMixins(intAttrs)],
    computed: {
        intValue: {
            get () {
                return Number(this.value)
            },
            set (val) {
                val = parseInt(val)
                this.updateForm(val)
            }
        }
    }
}
</script>
<style lang="scss" scoped>
.tag-input {
    /deep/ .el-input__inner {
        height: 36px;
        line-height: 36px;
    }
}
</style>
