<template>
    <div class="tag-textarea">
        <el-input
            type="textarea"
            v-model="textareaValue"
            :class="{'rf-view-textarea-value': !formMode}"
            :autosize="{minRows: formMode ? 3 : 1}"
            :disabled="!editable || !formMode"
            :rows="3"
            resize="none"
            :placeholder="placeholder">
        </el-input>
        <span v-show="!validateInfo.valid" class="common-error-tip error-info">{{validateInfo.message}}</span>
    </div>
</template>
<script>
import '@/utils/i18n.js'
import { getFormMixins } from '../formMixins.js'

const textareaAttrs = {
    value: {
        type: String,
        required: false,
        default: ''
    },
    placeholder: {
        type: String,
        required: false,
        default: '',
        desc: "placeholder"
    }
}
export default {
    name: "TagTextarea",
    mixins: [getFormMixins(textareaAttrs)],
    computed: {
        textareaValue: {
            get () {
                if (!this.formMode && !this.value) {
                    return '--'
                }
                return this.value
            },
            set (val) {
                this.updateForm(val)
            }
        }
    }
}
</script>
<style lang="scss">
    .rf-view-textarea-value {
        .el-textarea__inner {
            padding: 6px 0;
            border: none;
            resize: none;
            &[disabled = "disabled"] {
                background: inherit;
                color: inherit;
                cursor: text;
            }
        }
    }
</style>



