<template>
    <div class="tag-checkbox">
        <div v-if="formMode">
            <el-checkbox-group v-model="checkedValue">
                <div class="checkbox-item" v-for="item in items" :key="item.name">
                    <el-checkbox :label="item.value" :disabled="!editable">{{item.name}}</el-checkbox>
                </div>
            </el-checkbox-group>
            <span v-show="!validateInfo.valid" class="common-error-tip error-info">{{validateInfo.message}}</span>
        </div>
        <span v-else class="rf-view-value">{{viewValue}}</span>
    </div>
</template>
<script>
import '@/utils/i18n.js'
import { getFormMixins } from '../formMixins.js'

const checkboxAttrs = {
    items: {
        type: Array,
        required: true,
        desc: "array like [{name: '', value: ''}, {name: '', value: ''}]"
    },
    value: {
        type: Array,
        required: false,
        default () {
            return []
        }
    }
}
export default {
    name: 'TagCheckBox',
    mixins: [getFormMixins(checkboxAttrs)],
    computed: {
        checkedValue: {
            get () {
                return this.value
            },
            set (val) {
                this.updateForm(val)
            }
        },
        viewValue () {
            if (!this.checkedValue.length) {
                return '--'
            }

            return this.checkedValue.map(val => {
                let label = val
                this.items.some(item => {
                    if (item.value === val) {
                        label = item.name
                        return true
                    }
                })
                return label
            }).join(',')
        }
    }
}
</script>
<style lang="scss" scoped>
    .checkbox-item {
        display: inline-block;
        min-width: 100px;
        height: 36px;
        line-height: 36px;
        /deep/ .el-checkbox__input.is-checked + .el-checkbox__label {
            color: #606266;
        }
    }
</style>
