import '@/utils/i18n.js'
import moment from 'moment'
const draft = {
    // 添加草稿
    addDraft (username, ccId, templateId, templateData, message = gettext('自动保存')) {
        // 防止无法进行存储草稿 大约96KB左右的多余空间
        const minDraftLength = 100000
        // 草稿箱剩余大小
        let remainingLocalStorageSize = 1024 * 1024 * 5 - unescape(encodeURIComponent(JSON.stringify(localStorage))).length
        let index = 0
        while (remainingLocalStorageSize < minDraftLength) {
            // 大小不足96KB时清除一部分草稿 从最早创建的草稿开始删除
            let key = localStorage.key(index++)
            let keyArray = key.split('_')
            if (keyArray.length !== 4) {
                continue
            }
            localStorage.removeItem(key)
            remainingLocalStorageSize = 1024 * 1024 * 5 - unescape(encodeURIComponent(JSON.stringify(localStorage))).length
        }
        // 当前时间戳
        let timestamp = new Date().getTime()
        // 当前年月日时分秒时间
        let localTime = moment.unix(timestamp / 1000).format('YYYY-MM-DD HH:mm:ss')
        // 数据
        let descriptionData = {'time': localTime , 'message': message}
        // 存储数据
        let key = [username, ccId, templateId, timestamp].join('_')
        // 超过50个草稿需要进行删除
        let draftNumber = 0
        let localStorageLength = localStorage.length
        for (let index = localStorageLength - 1; index >= 0 ; index--) {
            let key = localStorage.key(index)
            // 获取key字段的所有切割信息
            let keyArray = key.split('_')
            if (keyArray.length === 4 && keyArray[1] === ccId && keyArray[2] === templateId && keyArray[0] === username) {
                draftNumber++
                if (draftNumber >= 50) {
                    this.deleteDraft(key)
                }
            }
        }
        try {
            localStorage[key] = JSON.stringify({'template': templateData , 'description': descriptionData})
        }
        catch (e) {
            // 用户浏览器不支持localStorage
        }
    },
    // 删除草稿
    deleteDraft (key) {
        try {
            localStorage.removeItem(key)
            return true
        }
        catch (e) {
            return false
        }
    },
    // 用于替换第一次创建模板id为 uuid 的id
    draftReplace (username, cc_id, template_id, templateUUID) {
        for (let key in localStorage) {
            // 获取key字段的所有切割信息
            let keyArray = key.split('_')
            // 切割完的长度应该为4且原先的模板id uuid 时才需要使用
            // username重复的几率比较大放置最后判断
            if (keyArray.length === 4 && keyArray[1] === cc_id && keyArray[2] === templateUUID && keyArray[0] === username) {
                // 原先的模板数据
                let draftData = localStorage[key]
                // 将原先信息删除
                localStorage.removeItem(key)
                keyArray[2] = template_id
                // 重新创建key值
                let newKey = keyArray.join('_')
                localStorage[newKey] = draftData
            }
        }
    },
    // 获取当前草稿箱
    getDraftArray (username, cc_id, template_id) {
        let draftArray = []
        let localStorageLength = localStorage.length
        for (let index = localStorageLength - 1; index >= 0 ; index--) {
            let key = localStorage.key(index)
            // 获取key字段的所有切割信息
            let keyArray = key.split('_')
            // 切割完的长度应该为4且为当前模板的相关参数
            // username重复的几率比较大放置最后判断
            // template_id 需要加上""变为字符串
            if (keyArray.length === 4 && keyArray[1] === cc_id && keyArray[2] === template_id + "" && keyArray[0] === username) {
                draftArray.push({'key': key,'data': JSON.parse(localStorage[key])})
            }
        }
        return draftArray
    },
    // 删除没有template_id 的模板
    deleteAllDraftByUUID (username, cc_id, uuid) {
        let localStorageLength = localStorage.length
        for (let index = localStorageLength - 1; index >= 0 ; index--) {
            let key = localStorage.key(index)
            let keyArray = key.split('_')
            // uuid 需要加上""变为字符串
            if (keyArray.length === 4 && keyArray[1] === cc_id && keyArray[2] === uuid + "" && keyArray[0] === username) {
                localStorage.removeItem(key)
            }
        }
    },
    // 复制并替换草稿（模板克隆时使用）
    copyAndReplaceDraft (username, cc_id, template_id, uuid) {
        for (let key in localStorage) {
            // 获取key字段的所有切割信息
            let keyArray = key.split('_')
            if (keyArray.length === 4 && keyArray[1] === cc_id && keyArray[2] === template_id + "" && keyArray[0] === username) {
                // 原先的模板数据
                let draftData = localStorage[key]
                keyArray[2] = uuid
                // 重新创建key值并进行创建
                let newKey = keyArray.join('_')
                localStorage[newKey] = draftData
            }
        }
    }
}

export default draft