<template>
    <div class="tag-radio">
        <div v-if="formMode">
            <el-radio-group v-model="checkedValue">
                <div class="radio-item" v-for="item in items" :key="item.name">
                    <el-radio :label="item.value" :disabled="!editable">{{item.name}}</el-radio>
                </div>
            </el-radio-group>
            <span v-show="!validateInfo.valid" class="common-error-tip error-info">{{validateInfo.message}}</span>
        </div>
        <span v-else class="rf-view-value">{{viewValue}}</span>
    </div>
</template>
<script>
import '@/utils/i18n.js'
import { getFormMixins } from '../formMixins.js'

const radioAttrs = {
    items: {
        type: Array,
        required: true,
        desc: "array like [{name: '', value: ''}, {name: '', value: ''}]"
    },
    value: {
        required: false,
        default: ''
    }
}
export default {
    name: 'TagRadio',
    mixins: [getFormMixins(radioAttrs)],
    computed: {
        checkedValue: {
            get () {
                return this.value
            },
            set (val) {
                this.updateForm(val)
            }
        },
        viewValue () {
            if (this.checkedValue === '') {
                return '--'
            }

            let label = this.checkedValue
            this.items.some(item => {
                if (item.value === this.checkedValue) {
                    label = item.name
                    return true
                }
            })
            return label
        }
    }
}
</script>
<style lang="scss" scoped>
    .radio-item {
        display: inline-block;
        /deep/ .el-radio__label {
            display: inline-block;
            padding-right: 10px;
            min-width: 80px;
        }
        .el-radio {
            height: 36px;
            line-height: 36px;
        }
    }
</style>
